import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ScrollEventsService } from '../../../../services/scroll-events.service';

@Component({
  selector: 'app-jumbotron',
  templateUrl: './jumbotron.component.html',
  styleUrls: ['./jumbotron.component.scss']
})
export class JumbotronComponent implements OnInit {
  @ViewChild('buttons') private buttons: ElementRef;
  constructor(
    private scrollEventsService: ScrollEventsService
  ) { }

  ngOnInit() {
    this.scrollEventsService.setButtonsPosition(this.buttons.nativeElement.offsetTop);
  }

}
