import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ScrollEventsService {
  private buttonsPosition = new Subject<number>();
  private pageOffset = new Subject<number>();
  private menuSetFixedInitValue = false;
  private menuSetFixed = new BehaviorSubject<boolean>(this.menuSetFixedInitValue);

  buttonsPosition$ = this.buttonsPosition.asObservable();
  pageOffset$ = this.pageOffset.asObservable();
  menuSetFixed$ = this.menuSetFixed.asObservable();

  setButtonsPosition(value: number) {
    this.buttonsPosition.next(value);
  }
  setPageOffset(value: number) {
    this.pageOffset.next(value);
  }

  setMenuSetFixed(value: boolean) {
    this.menuSetFixed.next(value);
  }
}
