import { Component, OnInit, Input } from '@angular/core';
import { ImageModel } from '../feature/feature.component';

@Component({
  selector: 'app-feature-vertical',
  templateUrl: './feature-vertical.component.html',
  styleUrls: ['./feature-vertical.component.scss']
})
export class FeatureVerticalComponent implements OnInit {
  @Input() title: string;
  @Input() medCalXTitle: string;
  @Input() medCalXText: string;
  @Input() qxmdTitle: string;
  @Input() qxmdText: string;
  @Input() images: ImageModel[];
  public activePlatform = 'med';
  public selectedImage: string;
  public medMobImg: string;
  public qxmdMobImg: string;

  private pixelratio: string;
  constructor() { }

  ngOnInit() {
    switch (this.pixelratio) {
      case '1x':
        this.medMobImg = this.images[0].src.replace('@2x', '');
        this.qxmdMobImg = this.images[1].src.replace('@2x', '');
        break;
      case 'retina':
        this.medMobImg = this.images[0].src;
        this.qxmdMobImg = this.images[1].src;
        break;
      case '4k':
        this.medMobImg = this.images[0].src.replace('@2x', '@3x');
        this.qxmdMobImg = this.images[1].src.replace('@2x', '@3x');
        break;
      default:
        this.medMobImg = this.images[0].src;
        this.qxmdMobImg = this.images[1].src;
        break;
    }
    this.images.map(item => {
      if (item.platform === 'med') {
        switch (this.pixelratio) {
          case '1x':
            this.selectedImage = item.src.replace('@2x', '');
            break;
          case 'retina':
            this.selectedImage = item.src;
            break;
          case '4k':
            this.selectedImage = item.src.replace('@2x', '@3x');
            break;
          default:
            this.selectedImage = item.src.replace('@2x', '');
            break;
        }
      }
    });
  }

  changePlatform(platform: string) {
    this.activePlatform = platform;
    this.images.map(item => {
      if (item.platform === platform) {
        switch (this.pixelratio) {
          case '1x':
            this.selectedImage = item.src.replace('@2x', '');
            break;
          case 'retina':
            this.selectedImage = item.src;
            break;
          case '4k':
            this.selectedImage = item.src.replace('@2x', '@3x');
            break;
          default:
            this.selectedImage = item.src.replace('@2x', '');
            break;
        }
      }
    });
  }

  thisUserAgent(event: any) {
    this.pixelratio = event.pixelratio;
  }
}
