import { Component, OnInit, Input } from '@angular/core';

export interface ImageModel {
  platform: string;
  src: string;
}

@Component({
  selector: 'app-feature',
  templateUrl: './feature.component.html',
  styleUrls: ['./feature.component.scss']
})
export class FeatureComponent implements OnInit {
  @Input() title: string;
  @Input() subTitle: string;
  @Input() text: string;
  @Input() multiPlatform = true;
  @Input() images: ImageModel[];
  public activePlatform = 'ios';
  public toggleBgWidth = '79px';
  public toggleBgLeft = '0px';
  public selectedSrc: string;

  private pixelratio: string;
  constructor() { }

  ngOnInit() {
    this.images.map(item => {
      if (item.platform === 'ios') {
        switch (this.pixelratio) {
          case '1x':
            this.selectedSrc = item.src.replace('@2x', '');
            break;
          case 'retina':
            this.selectedSrc = item.src;
            break;
          case '4k':
            this.selectedSrc = item.src.replace('@2x', '@3x');
            break;
          default:
            this.selectedSrc = item.src.replace('@2x', '');
            break;
        }
      }
    });
  }

  changePlatform(event: MouseEvent, platform: string) {
    this.toggleBgLeft = `${(event.target as HTMLElement).offsetLeft}px`;
    this.toggleBgWidth = `${(event.target as HTMLElement).clientWidth}px`;
    this.activePlatform = platform;
    this.images.map(item => {
      if (item.platform === platform) {
        switch (this.pixelratio) {
          case '1x':
            this.selectedSrc = item.src.replace('@2x', '');
            break;
          case 'retina':
            this.selectedSrc = item.src;
            break;
          case '4k':
            this.selectedSrc = item.src.replace('@2x', '@3x');
            break;
          default:
            this.selectedSrc = item.src.replace('@2x', '');
            break;
        }
      }
    });
  }

  thisUserAgent(event: any) {
    this.pixelratio = event.pixelratio;
  }
}
