import { Component, OnDestroy, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ScrollEventsService } from '../../../../services/scroll-events.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-fixed-menu',
  templateUrl: './fixed-menu.component.html',
  styleUrls: ['./fixed-menu.component.scss']
})
export class FixedMenuComponent implements OnInit, OnDestroy {
  @ViewChild('fixedMenu') private menu: ElementRef;
  @ViewChild('anchor') private anchor: ElementRef;
  public isFixed = false;
  public isRelative = false;
  private pageOffsetListener: Subscription;
  constructor(
    private scrollEventsService: ScrollEventsService
  ) { }

  ngOnInit() {
    this.pageOffsetListener = this.scrollEventsService.pageOffset$.subscribe(value => {
      if (this.anchor.nativeElement.getBoundingClientRect().top <= 80) {
        this.isFixed = true;
        this.isRelative = false;
      } else {
        this.isRelative = this.anchor.nativeElement.getBoundingClientRect().top + 60 <= window.innerHeight;
        this.isFixed = false;
      }
      this.scrollEventsService.setMenuSetFixed(this.isFixed);
    });
  }

  ngOnDestroy() {
    this.pageOffsetListener.unsubscribe();
  }
}
