import { Component, OnDestroy, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ScrollEventsService } from '../../../../services/scroll-events.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {
  public selectLangIsOpen = false;
  public selectedLangText = 'English';
  public selectedLangTextShort = 'Eng';
  public showButtons = false;
  public haveFixedMenu = false;

  private pageOffsetListener: Subscription;
  private buttonsPositionListener: Subscription;
  private menuSetFixedListener: Subscription;
  private buttonsPosition = 0;
  constructor(
    private translateService: TranslateService,
    private scrollEventsService: ScrollEventsService
  ) { }

  ngOnInit() {
    this.pageOffsetListener = this.scrollEventsService.pageOffset$.subscribe(value => {
      this.showButtons = value > this.buttonsPosition + 50;
    });
    this.buttonsPositionListener = this.scrollEventsService.buttonsPosition$.subscribe(value => {
      this.buttonsPosition = value;
    });
    this.menuSetFixedListener = this.scrollEventsService.menuSetFixed$.subscribe(value => {
      this.haveFixedMenu = value;
    });
  }

  ngOnDestroy() {
    this.pageOffsetListener.unsubscribe();
    this.buttonsPositionListener.unsubscribe();
    this.menuSetFixedListener.unsubscribe();
  }

  changeLanguage(event: MouseEvent, lang: string, forShort = false) {
    event.stopPropagation();
    if (lang === 'en') {
      this.selectedLangText = 'English';
    } else {
      this.selectedLangText = 'France';
    }
    if (lang === 'en' && forShort) {
      this.selectedLangTextShort = 'Eng';
    } else if (lang === 'fr' && forShort) {
      this.selectedLangTextShort = 'Frn';
    }
    this.translateService.use(lang);
    this.selectLangIsOpen = false;
  }
}
